import {ICharacter, IGetCharacters, IInfo} from "../types/character.types";
import {instanceRaM} from "./api";

export const charactersApi = {
    getAll: async (page: string) => {
        let response = await instanceRaM.get<IGetCharacters>(`character/?page=${page}`);
        return response.data;
    },
    getById: async (id: string) => {
        let response = await instanceRaM.get<ICharacter>(`character/${id}`);
        return response.data;
    },
    getMultipleItems: async (ids: string[]) => {
        const url = ids.reduce( (prev, curr) => prev + String(curr) + ",", "")
        let response = await instanceRaM.get<ICharacter[]>(`character/${url}`);
        return response.data;
    },
    getInfo: async () => {
        let response = await instanceRaM.get<IGetCharacters>("character");
        return response.data.info
    }
}